@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@100;200;300;400;500;700&display=swap);
* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}

:root {
  --accent-color:  hsl(180, 66%, 49%);
  --primary-color: hsl(257, 7%, 63%);
  --light-bg: hsl(0, 0%, 95%);
  --dark-color: hsl(255, 11%, 22%);
  --purple-bg: hsl(257, 27%, 26%);
  --error-color: hsl(0, 87%, 67%);
}

.App {
  font-family: 'Poppins', sans-serif;
  position: relative;
}

.container {
  max-width: 1100px;
  margin: 0 auto;
}
.header {
	margin-bottom: 50px;
	padding: 1rem;
}

.menu-btn {
	width: 30px;
	height: 30px;
	padding: 5px;
	position: absolute;
	top: 20px;
	right: 15px;
	z-index: 99;
	display: none;
	flex-direction: column;
	align-items: center;
	justify-content: space-between;
	cursor: pointer;
}

.menu-btn .bar {
	width: 100%;
	height: 2px;
	background-color: #888;
	border-radius: 2px;
}

.navigation {
	display: flex;
	align-items: center;
	justify-content: space-between;
	padding: 15px;
	margin-bottom: 80px;
	height: 60px;
}

.navigation .nav-inner {
	display: flex;
	align-items: center;
	justify-content: flex-start;
	width: 100%;
	z-index: 100;
}

.logo {
	margin-right: 30px;
}

.links-wrapper {
	display: flex;
	align-items: center;
	justify-content: space-between;
	width: 100%;
}

.nav-links {
	list-style-type: none;
	display: flex;
	align-items: center;
	justify-content: center;
}

.nav-link {
	margin-right: 15px;
	color: var(--primary-color);
	font-size: 15px;
	cursor: pointer;
	transition: 0.2s ease;
}

.nav-link:hover {
	color: var(--dark-color);
}

.nav-buttons {
	display: flex;
	align-items: center;
	justify-content: space-between;
}

.btn{
	padding: 10px 15px;
	font-family: inherit;
	font-size: 15px;
	cursor: pointer;
	border: none;
	margin-right: 10px;
	font-weight: 600;
}

#login-btn {
	background-color: transparent;
}

.btn-accent {
	background-color: var(--accent-color);
	color: #fff;
	border-radius: 20px;
	transition: 0.2s ease;
}

.btn-accent:hover {
	opacity: 0.8;
}

.banner {
	display: grid;
	grid-template-columns: repeat(2, 1fr);
	align-items: center;
	justify-content: center;
	grid-gap: 40px;
}

.write h1 {
	color: var(--dark-color);
	line-height: 1.2;
	font-size: 4.2rem;
	margin-bottom: 10px;
}

.write h4 {
	font-size: 1.3rem;
	color: var(--primary-color);
	margin-bottom: 30px;
	font-weight: 400;
	line-height: 1.6;
}

.write .btn {
	padding: 15px 30px;
	border-radius: 25px;
	font-size: 17px;
}

.banner-img {
	max-width: 100%;
}



@media (max-width: 700px) {

	.menu-btn {
		display: flex
	}

	.links-wrapper {
		display: block;
		position: absolute;
		z-index: 2;
		top: 70px;
		right: 5vw;
		height: auto;
		width: 90vw;
		background-color: var(--purple-bg);
		padding: 20px;
		border-radius: 15px;
		text-align: center;
		-webkit-transform: translateY(-500px);
		        transform: translateY(-500px);
		transition: all 0.4s ease-out;
	}

	.links-wrapper.show {
		-webkit-transform: translateY(0);
		        transform: translateY(0);
	}

	.nav-links {
	 	display: block;
	 	border-bottom: 1px solid #ccc;
	 	padding: 10px;
	 }

	 .nav-link {
	 	color: #fff;
	 	margin-bottom: 20px;
	 	font-size: 1rem;
	 }

	 .nav-buttons {
	 	display: flex;
	 	flex-direction: column;
	 	align-items: center;
	 	justify-content: center;
	 	padding: 20px;
	 }

	 .nav-buttons button {
	 	display: block;
	 	color: #fff;
	 	font-size: 1rem;
	 	margin: 5px 0;
	 	width: 100%;
	 }

	.banner {
		display: flex;
		flex-direction: column;
	}

	.write {
		order: 2;
		text-align: center
	}

	.banner-img {
		order: 1;
	}

	.write h1 {
		line-height: 1.2;
		font-size: 1.8rem;
		margin-bottom: 5px
	}

	.write h4 {
		font-size: 1rem;
		margin-bottom: 20px;
		line-height: 1.5;
	}

	.write .btn {
		padding: 10px 25px;
		border-radius: 25px;
		font-size: 15px;
	}

}
.link-shortener {
	background-color: var(--light-bg);
	padding: 20px;
}

.link-box {
	background: url(/url-shortener/static/media/bg-shorten-desktop.0d9742f4.svg) no-repeat center center/cover;
	background-color: var(--purple-bg);
	max-width: 800px;
	margin: 0 auto;
	display: flex;
	align-items: flex-start;
	justify-content: space-around;
	padding: 1.5rem;
	border-radius: 10px;
	-webkit-transform: translateY(-40px);
	        transform: translateY(-40px);
}

.input-container {
	display: flex;
	flex-direction: column;
	align-items: stretch;
	justify-content: center;
	width: 80%;
}

.link-box input {
	padding: 10px;
	height: 45px;
	width: 100%;
	font-family: inherit;
	font-size: 17px;
	border-radius: 5px;
	border: none;
	outline: none;
	margin: 0 20px 0 0;
}

.link-box input.err {
	border: 2px solid var( --error-color);
}

.link-box input.err::-webkit-input-placeholder {
	color: var(--error-color);
	opacity: 0.7;
}

.link-box input.err:-ms-input-placeholder {
	color: var(--error-color);
	opacity: 0.7;
}

.link-box input.err::placeholder {
	color: var(--error-color);
	opacity: 0.7;
}

.error-msg {
	font-size: 13px;
	font-style: italic;
	color: var( --error-color);
	-webkit-transform:translateY(5px);
	        transform:translateY(5px);
}

#shorten-btn {
	background-color: var(--accent-color);
	color: #fff;
	border-radius: 5px;
	height: 45px;
	font-size: 17px;
	width: auto;
}

#shorten-btn:hover {
	opacity: 0.9;
}

#shorten-btn.loading {
	opacity: 0.8;
}

.shortened-link {
	display: flex;
	align-items: center;
	justify-content: space-between;
	background-color: #fff;
	padding: 20px;
	border-radius: 8px;
	margin-top: 20px;
}

.shortened-link > div {
	display: flex;
	align-items: center;
}

.shortened-link .short-link {
	color: var(--accent-color);
	margin-right: 10px;
}

.shortened-link .copy-btn {
	background-color: var(--accent-color);
	color: #fff;
	cursor: pointer;
	padding: 10px;
	border-radius: 5px;
	border: none;
	font-family: inherit;
}

.shortened-link .copy-btn.copying {
	background-color: var(--purple-bg);
}



@media(max-width: 700px) {
	.link-shortener {
		padding: 0 1rem;
	}

	.link-box {
		flex-direction: column;
		background: url(/url-shortener/static/media/bg-shorten-mobile.41c34b33.svg) no-repeat center center/cover;
		background-color: var(--purple-bg);
	}

	.input-container {
		width: 100%;
	}

	.link-box input {
		width: 100%;
		margin: 0;
	}

	#shorten-btn {
		margin: 1rem 0 0;
		width: 100%;
	}

	.shortened-link {
		flex-direction: column;
		align-items: stretch;
	}

	.shortened-link .link {
		padding-bottom: 10px;
		border-bottom: 1px solid#888;
	}

	.shortened-link > div {
		flex-direction: column;
		align-items: stretch;
		padding-top: 10px;
	}

	.shortened-link .copy-btn {
		width: 100%;
		margin-top: 10px;
	}
}
.stats {
	padding: 1rem 1rem 6rem 1rem;
	background-color: var(--light-bg);
}

.heading {
	display: flex;
	align-items: center;
	justify-content: center;
	flex-direction: column;
	text-align: center;
	padding: 2rem;
	margin-bottom: 2rem;
}

.heading h2 {
	color: var(--dark-color);
	font-size: 2.5rem;
	margin-bottom: 20px;
}

.heading p {
	color: var(--primary-color);
	font-size: 18px;
}

.grid {
	display: grid;
	grid-template-columns: repeat(3, 1fr);
	grid-gap: 40px;
}

.stat {
	background-color: #fff;
	border-radius: 10px;
	padding: 30px;
	display: flex;
	flex-direction: column;
	align-items: flex-start;
	text-align: left;
}

.stat img {
	background-color: var(--purple-bg);
	padding: 15px;
	border-radius: 50%;
	-webkit-transform: translateY(-50px);
	        transform: translateY(-50px);
}

.stat h3 {
	color: var(--dark-color);
	margin-bottom: 20px;
	font-size: 22px;
}

.stat p {
	color: hsl(257, 7%, 63%);
	line-height: 1.6;
}

.stat:nth-child(2) {
	-webkit-transform: translateY(20px);
	        transform: translateY(20px);
}

.stat:nth-child(3) {
	-webkit-transform: translateY(40px);
	        transform: translateY(40px);
}

.boost {
	background: url(/url-shortener/static/media/bg-boost-desktop.772154c9.svg) no-repeat center center/cover;
	background-color: var(--purple-bg);
	padding: 1rem;
	height: 200px;
	text-align: center;
	display: table-footer-group;
	display: flex;
	flex-direction: column;
	align-items: center;
	justify-content: center;
}

.boost h2 {
	color: #fff;
	font-size: 2.5rem;
	margin-bottom: 20px;
}

.boost-btn {
	background-color: var(--accent-color);
	padding: 10px 30px;
	font-weight: 700;
	color: #fff;
	border-radius: 25px;
	border: none;
	outline: none;
	cursor: pointer;
	font-family: inherit;
	font-size: 18px;
}

.boost-btn:hover {
	opacity: 0.8;
}

@media(max-width: 700px) {

	.heading {
		padding: 1rem;
		margin-bottom: 3rem;
	}

	.heading h2 {
		font-size: 1.8rem;
		margin-bottom: 15px;
	}

	.heading p {
		font-size: 16px;
		line-height: 1.5;
	}

	.grid {
		grid-template-columns: 1fr;
	}

	.stat {
		align-items: center;
		text-align: center;
	}

	.stat p {
		font-size: 15px;
	}

	.boost {
		background: url(/url-shortener/static/media/bg-boost-mobile.4b04bd89.svg) no-repeat center center/cover;
		background-color: var(--purple-bg);
	}

	.boost h2 {
		font-size: 1.8rem;
		margin-bottom: 20px;
	}

	.boost-btn {
		padding: 10px 25px;
		font-size: 16px;
	}

}
.footer {
	background-color: var(--dark-color);
	padding: 2rem 1rem;
}

.footer-inner {
	display: grid;
	grid-template-columns: 2fr 3fr 2fr;
	grid-gap: 30px;
	justify-content: center;
}

.links {
	display: grid;
	grid-template-columns: repeat(3, 1fr);
	grid-gap: 20px;
	color: #fff;
}

.links ul {
	list-style: none;
}

.links ul li {
	font-weight: 300;
	margin-bottom: 10px;
	font-size: 13px;
	cursor: pointer;
}

.links ul li:hover {
	color: var(--accent-color);
}


.links ul li:first-child {
	font-weight: 500;
	font-size: 15px;
	margin-bottom: 20px;
	cursor: default;
}

.links ul li:first-child:hover {
	color: #fff;
}

.logo {
	font-size: 30px;
	font-weight: 700;
	color: #fff;
}

.socials {
	display: flex;
	align-items: flex-start;
}

.socials img {
	margin-right: 20px;
	cursor: pointer;
	padding-bottom: 10px;
}

.socials img:hover {
	border-bottom: 2px solid var(--accent-color);
}

@media(max-width: 700px) {
	.footer-inner {
		grid-template-columns: 1fr;
		text-align: center;
	}

	.logo {
		text-align: center;
		display: flex;
		align-items: center;
		justify-content: center;
	}

	.socials {
		align-items: center;
		justify-content: center;
	}

	.links {
		grid-template-columns: 1fr;
	}
}

